
import { defineComponent } from "vue";

export default defineComponent({
  name: "home",
  components: {},
  setup() {
    const clickTwitter = () => {
      location.href = "https://twitter.com/home";
    };

    const clickGithub = () => {
      location.href = "https://github.com/tesso57";
    };
    return { clickTwitter, clickGithub };
  }
});
